import React from 'react'
import foto1 from"../assets/img/foto1.jpeg";
import foto20 from"../assets/img/images.jpeg";
export const Tramites = () => {
    return (
        <div>
            <div class="container">
                <div class="row align-items-start">
                    <div class="col">
                        las tramites se hecen previa coordinacion con secretaria.
                    </div>
                    <div class="col">
                        si  tiene alguna consultas puede llamar  a los numeros de consulta.  
                    </div>
                    <div class="col">
                        los tramites  para los certificados se  veran dicha coordinacion de secretaria.
                    </div>
                </div>
                <div class="row align-items-center">
                    <div class="col">
                        se  recomienda presentar sus documenmtos bien hechos. 
                    </div>
                    <div class="col">
                        los documentos se  deben  entregar en un folder a  previa secretaria.
                    </div>
                    <div class="col">
                        One of three columns
                    </div>
                </div>
            </div>
            <h1>TRAMITES DE ACREDITACION </h1>
            <p>Aqui  podras  ver la tramites  a  final  de aver  termino el curso de idioma que haya seleccionado</p>
            <div id="carouselExample" class="carousel slide">
                <div class="carousel-inner">
                    <div class="carousel-item active">
                        <img src={foto1} class="d-block w-100" width="400" height="400"/>
                    </div>
                    <div class="carousel-item">
                        <img src={foto20} class="d-block w-100" width="400" height="400"/>
                    </div>
                </div>
                <button class="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Previous</span>
                </button>
                <button class="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Next</span>
                </button>
            </div>
            <p>CUANDO  PUEDES SER  TU  CERTIFICADO DE ACREDITACION</p>
            Estudiantes de Grado, Master universitario, o Doctorado matriculados en la US en el curso correspondiente y los estudiantes que sin estar matriculados este curso académico tengan el 100% de los créditos superados y les falte acreditar el idioma para cerrar expediente.  Una vez resuelta la solicitud, podrás consultar tus idiomas acreditados en Secretaria Virtual en el apartado de idiomas superados/acreditados.
            PAS y PDI de la US en activo.  Se grabará en el Registro de Personal.  Puedes consultar tus idiomas acreditados en tu ficha personal.
            <div class="container">
                <h1>Documentación que debes presentar</h1>
                <ul class="list-group">
                    <li class="list-group-item">Solicitud de acreditación de competencias lingüísticas cumplimentada y firmada por el interesado</li>
                    <li class="list-group-item">Certificado o título oficial original (no sirven los provisionales). Certificados/títulos reconocidos</li>
                    <li class="list-group-item">Original del DNI</li>
                    <li class="list-group-item">Si la solicitud la realiza otra persona en tu nombre, Autorización para presentar la documentación, con copia de tu DNI y original de la persona que la presenta en tu nombre</li>
                    <li class="list-group-item">Los estudiantes de CEU Cardenal Spínola deberán presentar además certificado expedido por el Centro de estar matriculado en el presente curso académico o tener superados todos los créditos de su titulación y de no tener acreditado otro idioma</li>
                    <li class="list-group-item">Dónde se presenta:</li>
                    <ul class="list-group">
                        <li class="list-group-item">Secretaría del IDI (debes solicitar cita previa)</li>
                        <li class="list-group-item">Registro de la US (Rectorado o Pabellón de Brasil) (debes solicitar cita previa)</li>
                        <li class="list-group-item">Cualquier registro de las Administraciones Públicas (Registros de la Junta de Andalucía, Ayuntamientos, etc. debiendo aportar la documentación original para digitalización como copia auténtica. Consultar con el Organismo si tienen cita previa)</li>
                        <li class="list-group-item">Por correo postal. Por esta vía es necesario enviar la solicitud de acreditación junto con copia compulsada del diploma y DNI (las compulsas se realizan por un Organismo público o Notario)</li>
                    </ul>
                    <li class="list-group-item">Estas opciones están reguladas por el artículo 16.4 de la Ley 39/2015, de 1 de octubre, de Procedimiento Administrativo Común de las Administraciones Públicas</li>
                </ul>
            </div>
            <div class="container">
                <h1>Registro de Recepción de Acreditación</h1>
                <form id="formularioAcreditacion">
                    <div class="form-group">
                        <label for="nombre">Nombre:</label>
                        <input type="text" class="form-control" id="nombre" required/>
                    </div>
                    <div class="form-group">
                        <label for="apellido">Apellido:</label>
                        <input type="text" class="form-control" id="apellido" required/>
                    </div>
                    <div class="form-group">
                        <label for="correo">Correo Electrónico:</label>
                        <input type="email" class="form-control" id="correo" required/>
                    </div>
                    <div class="form-group">
                        <label for="fechaAcreditacion">Fecha de Recepción de Acreditación:</label>
                        <input type="date" class="form-control" id="fechaAcreditacion" required/>
                    </div>
                    <div class="form-group">
                        <label for="comentarios">Comentarios adicionales:</label>
                        <textarea class="form-control" id="comentarios" rows="3"></textarea>
                    </div>
                    <button type="submit" class="btn btn-primary">Registrar Acreditación</button>
                </form>
            </div>
            <iframe width="560" height="315" src="https://www.youtube.com/embed/KjNz4OMu1eo?si=76W9SePzOLA1Ad0p" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
            
        </div>
    )
}
