import React from 'react'
import foto7 from"../assets/img/images (1).jpeg";
import foto6 from"../assets/img/FOTO6.jpeg";

export const Matriculados = () => {
    return (
        <div>
            <div class="container">
                <div class="row align-items-center">
                    <div class="col">
                        la matriculacion hacerse en la universidad de Sevilla.
                    </div>
                    <div class="col">
                        El objetivo este la  matriculacion  es  para registrar  tus datos y podras ver tus calificaiones 
                    </div>
                    <div class="col">
                        Si  tienes dudas de la matriculacion debes contactarte a nuestros numeros.
                    </div>
                </div>
                <div class="row align-items-end">
                    <div class="col">
                        te recomendamos este instituro para formes un futuro linguistico
                    </div>
                    <div class="col">
                        aprende y logra sus exsitos  
                    </div>
                    <div class="col">
                        Te  motivo aprender buenos idionas 
                    </div>
                </div>
            </div>
            <h1>MATRICULACION</h1>
            <div id="carouselExample" class="carousel slide">
                <div class="carousel-inner">
                    <div class="carousel-item active">
                        <img src={foto7} class="d-block w-100" width="300" height="410"/>
                    </div>
                    <div class="carousel-item">
                        <img src={foto6} class="d-block w-100" width="300" height="410"/>
                    </div>

                </div>
                <button class="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Previous</span>
                </button>
                <button class="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                    <span class="visually-hidden">Next</span>
                </button>
            </div>
            <p>En este apartado podras  matricularte  como estudiante que esta dentro  de este  instituto
                la  podras  tener benecifios  en  los  libhros  a  utilizar  que  tienen ciertos codigos de uso propio.
            </p>
            <h1>REQUISITOS PARA MATRICULARTE</h1>

            <p>Tener o cumplir 16 años en el periodo de formalización de matrícula del curso de idiomas en cualquiera de sus modalidades.
            No podrás matricularte en tu lengua materna.</p>
            <h1>COMO MATRICULARTE EN NUESTROS CURSOS</h1>
            <p>El  primer paso para realizar tus estudios en el IDI es ver los tipos de cursos que ofrece el idioma en el que estás interesado y consultar los horarios para encontrar información de los grupos disponibles, lugar, días y horas de impartición. Y también puedes escoger entre la matrícula oficial (con derecho a asistir a clase presencial u online ) o matrícula libre (con derecho solo al examen final del curso).

            Aspectos a tener en cuenta:

            Si no tienes conocimientos previos del idioma, podrás matricularte directamente en el primer nivel sin necesidad de hacer prueba de nivel.
            Si tienes conocimientos previos del idioma, pueden darse 4 situaciones:
            Que no tengas ningún diploma oficial de ese idioma: Será necesario que realices una prueba de nivel que suele llevarse a cabo varias semanas antes del comienzo del curso y que requiere una matrícula específica para dicha prueba. Más información en esta página.
            Que en el expediente de la US ya aparezca el nivel que tienes, bien porque lo has acreditado para tu titulación de Grado o Máster, o porque lo has superado con nuestros cursos anteriores. En este caso podrás realizar la Automatrícula del IDI para el nivel siguiente.
            Que seas estudiante de la US y tengas un diploma oficial que acredite el nivel pero todavía no lo hayas acreditado en el IDI. En este caso es imprescindible que solicites la acreditación de este nivel para que te permita realizar la Automatrícula al nivel siguiente.
            Que no seas alumno de la US pero tengas un diploma oficial de idiomas. Tendrás que solicitar la convalidación de estudios en los plazos establecidos para cursar el nivel siguiente.
            Hay dos maneras de realizar el trámite de matrícula: sistema de automatrícula o presencialmente. La automatrícula es obligatoria para todos los estudiantes que dispongan de un UVUS (usuario virtual de la US), generalmente estudiantes actuales del Instituto de Idiomas que estén cursando otros estudios en la Universidad de Sevilla. Si no tienes UVUS, puedes realizar tu matrícula presenciamente con cita previa.
            Para más información, te sugerimos leer más sobre los tipos de matrícula disponibles en IDI, los plazos de matrícula y los precios .</p>

            <div class="container">
                <h1>Registro de Matriculación</h1>
                <form id="formularioMatriculacion">
                    <div class="form-group">
                        <label for="nombre">Nombre:</label>
                        <input type="text" class="form-control" id="nombre" required/>
                    </div>
                    <div class="form-group">
                        <label for="apellido">Apellido:</label>
                        <input type="text" class="form-control" id="apellido" required/>
                    </div>
                    <div class="form-group">
                        <label for="correo">Correo Electrónico:</label>
                        <input type="email" class="form-control" id="correo" required/>
                    </div>
                    <div class="form-group">
                        <label for="telefono">Teléfono:</label>
                        <input type="tel" class="form-control" id="telefono" required/>
                    </div>
                    <div class="form-group">
                        <label for="idioma">Idioma a Estudiar:</label>
                        <select class="form-control" id="idioma" required>
                            <option value="ingles">Inglés</option>
                            <option value="espanol">Español</option>
                            <option value="frances">Francés</option>
    
                        </select>
                    </div>
                    <div class="form-group">
                        <label for="nivel">Nivel de Idioma:</label>
                        <select class="form-control" id="nivel" required>
                            <option value="basico">Básico</option>
                            <option value="intermedio">Intermedio</option>
                            <option value="avanzado">Avanzado</option>
                        </select>
                    </div>
                    <button type="submit" class="btn btn-primary">Enviar Matrícula</button>
                </form>
            </div>
            <h1>DEBES MATRICULARTE EN DICHA UNIVERSIDAD  Y ESA ES LA MATRICULA SE  USA  EN INSTITUTO </h1>
            <p>Aqui  te muestro  los pasos</p>
            <iframe width="560" height="315" src="https://www.youtube.com/embed/x_fDLfoIGl4?si=zoWPEdd_s6kpZ8PC" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
        </div>
    )
}
